/*订单页面的样式*/

/* smallNav */

.smallNav{
	margin-top: 20px;
	border-bottom: 1px solid #ddd;
	padding-bottom: 20px;
}
.smallNav a.hover {
	font-size: 12px;
}
.smallNav a.hover:hover{
	color: #FD0031;
	
}
.dd_address{
	height: 80px;
	padding: 10px;
	border:1px solid #ccc;
	border-radius: 5px;
	background-color: #ddd;
	
}
.dd_address ul{
	height: 40px;
	margin: 20px 60px 0 50px;

}
.dd_address ul li{
	width: 350px;
	height: 40px;
	float: left;
	margin-left: -2px;

}
.dd_address ul li.first{
	margin-left: 20px;
}
.dd_address ul li span{
	display: inline-block;
}
.dd_address ul li span.btn{
	width: 36px;
	width: 36px;
	border-radius: 20px;
	text-align: center;
	line-height: 36px;
	background: #ddd;
	border: 2px solid #666;
}
.dd_address ul li span.btn-active{
	background: #ffccff;
    border: 2px solid #ff3399;
    color: #ff3399;
}
.dd_address ul li span.txt-active{
	color: #ff3399;
}
.dd_address ul li span.line{
	background: #666;
}
.content .dd_address ul li span.line-active{
	background: #ff3399;
}
.dd_address ul li span.txt {
	width: 70px;
	height: 40px;
	text-align: center;
	font-size: 14px;
	line-height: 40px;
}

.dd_address ul li span.line{
	width: 231px;
	height: 3px;
	background: #666;
}
.dd_address ul li.last span.line{
	height: 0;
}
.dd_address ul li.second span.txt{
	width: 110px;
}
.dd_address ul li.second span.line{
	width: 190px;
}
.content .dd_address ul li.second span.line-active{
	background: -webkit-gradient(linear,left center,right center,from(#ff3399), to(#666));
}

/* dd-success */

#dd_success .dd_address ul li.second span.line-active{
	background: #ff3399;
}

#dd_success .dd_address ul li.last span.txt{
	color: #ff3399;
}
#dd_success .dd_address ul li.last span.btn-active{
    background: #ffccff;
    border: 2px solid #ff3399;
    color: #ff3399;
}
/* 订单页面样式 */

#dd .dd_address ul li.first span.line-active{
	background: -webkit-gradient(linear,left center,right center,from(#ff3399), to(#666));
}
#dd .ddList ul{
    height: 40px;
    border: 1px solid #ccc;
    width: 1200px;
    margin: 30px  auto 0;
    background: #ddd;
  
}
#dd .ddList ul li{
	width: 151px;
	height: 40px;
	line-height: 40px;
	float: left;
	margin-right: 2px;
	text-align: center;
}
#dd .ddList ul li.first{
	width: 280px;
}
#dd .ddList ul.itemlist {
	margin-top: 0;
	height: 190px;
	background-color: #fff;
	border-top: none;
	border-right: none;
	border-left: none;
}
#dd .ddList ul.itemlist li.first a.pic {
	line-height: 24px;
    margin-top: 23px;
    display: block;
}
#dd .ddList ul.itemlist li.first a.pic img{
	width: 100px;
	height: 100px;
}
#dd .ddList ul.itemlist li.item{
	line-height: 200px;
}
#dd .ddList ul.itemlist li.item input.num {
    width: 33px;
    border-radius: 6px;
    border: 1px solid #ddd;
    outline: none;
}
#dd .ddList ul.itemlist li.item a.hander {
	color: #F42760;
}
#dd .dd_end {
	width: 1200px;
	height: 140px;
	margin: 30px auto;
	border: 1px solid #ddd;
}
#dd .dd_end p {
	margin-left: 20px;
	margin-top: 25px;
}
#dd .dd_end p.info span{
	color: #F42760;
	font-size: 18px;
}
#dd .dd_end p a{
	display: inline-block;
	height: 37px;
	text-align: center;
	line-height:37px;
}
#dd .dd_end p.info a{
	width: 100px;
	float: right;
	margin-right: 20px;
	background-color: #FF3366;
	color: #fff;
	border-radius: 3px;
}
#dd .dd_end p.continue{
	margin-top: 40px;
}
#dd .dd_end p.continue a{
	
	width: 100px;
	float: right;
	margin-right: 20px;
	background-color: #FF3366;
	color: #fff;
	border-radius: 3px;
}
#dd .dd_end p.continue a.fl{
	float: left;
	background-color: #ddd;
	border: 1px solid #ccc;
	color: #000;
}
#dd .dd_end p.continue a.fr{
	float: right;
	width: 180px;
	background-color: #E43D40;
}

/* 商品列表信息 */

#dd-xq .goodsList {
	width: 1200px;
	height: 1607px;
	margin: 10px auto;
	border: 1px solid #ccc;
}
#dd-xq .goodsList h3{
	height: 38px;
	line-height: 38px;
	text-indent: 20px;
	background-color: #ddd;
	
}
#dd-xq .goodsList h3 a{
	float: right;
	color: #F4673E;
	margin-right: 10px;
	font-size: 14px;
}
#dd-xq .goodsList table {
	width: 1170px;
	height: 220px;
	margin: 10px auto;
	border: 1px solid #ccc;
	border-collapse: collapse;
}
#dd-xq .goodsList table tr{
	border: 1px solid #ccc;
	height: 30px;
}
#dd-xq .goodsList table tr td{
	border: 1px solid #ccc;
	text-align: center;
}
#dd-xq .goodsList table tr td span.money {
	color: #F4673E;
	font-size: 18px;
}
#dd-xq .goodsList table tr td.bold{
	font-weight: bold;
}
#dd-xq .goodsList table tr td.bg{
	background: #EEE;
}
#dd-xq .goodsList table tr td.goods-ti{
	width: 250px;
}
#dd-xq .goodsList table.user-info tr{
	line-height: 0px;
	height: 15px;
}
#dd-xq .goodsList table.express-info tr td.last {
	text-align: right;
}
#dd-xq .goodsList table.express-info tr td input.ch{
    margin-right: 10px;
}
#dd-xq .goodsList table tr td.first{
	width: 37px;
}
#dd-xq .goodsList table.other-info tr td input.num{
	margin: 0 10px;
    width: 90px;
}
#dd-xq .goodsList table.other-info tr td input.hb{
	background: #FF6699;
    font-size: 12px;
    width: 80px;
    color: #fff;
    height: 20px;
    border-radius: 9px;
    outline: none;
    border: none;
    line-height: 20px;
    cursor: pointer;
}
#dd-xq .goodsList table.other-info tr td textarea.area {
	width: 800px;
	height: 100px;
	margin-top: 10px;
}

#dd-xq .goodsList table.paySum-info span{
	color: #FF3700;
	font-size: 16px;
	margin: 0 4px;
}

#dd-xq .goodsList table.paySum-info input.btn {
    width: 100px;
    height: 40px;
    border-radius: 3px;
    color: #fff;
    background: #FF3700;
    outline: none;
    border: none;
    cursor: pointer;
}
#dd-xq .goodsList table.paySum-info,#dd-xq .goodsList table.paySum-info tr,#dd-xq .goodsList table.paySum-info td {
	border: none;
}

#dd-xq .goodsList table.paySum-info td{
	text-align: right;
}
#dd-xq .goodsList table.paySum-info tr{
	height: 10px;
    line-height: 5px;
}

/* 收货人信息 */

#user-address-info .user-address{
	height: 530px;
	margin: 30px auto;
    box-shadow: 0px 0px 10px #666;
}
#user-address-info .user-address h3 {
	height: 30px;
	line-height: 30px;
	text-indent: 20px;
	background: #ccc;
	font-size: 16px;
}
#user-address-info .user-address  .addressCon {
	height: 400px;
	width: 1120px;
	margin: 45px auto 0;
}
#user-address-info .user-address  .addressCon p{
	border-bottom: 1px solid #ddd;
	height: 50px;
	line-height: 50px;
	margin-left: 40px;
}
#user-address-info .user-address  .addressCon p input{
	height: 25px;
	width: 250px;
	
}
#user-address-info .user-address  .addressCon p select {
	margin-left: 40px;
}
#user-address-info .user-address  .addressCon p span.con-info {
	text-align: right;
    padding-right: 10px;
    width: 123px;
    display: inline-block;
}
#user-address-info .user-address  .addressCon p span.info-r {
	margin-left: 50px;
}
#user-address-info .user-address  .addressCon p.btn {
	margin-top: 20px;
	text-align: center;
	border-bottom: none;
}
#user-address-info .user-address  .addressCon p.btn input{
	width: 300px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	border-radius: 3px;
	background-color: #FF6666;
	color: #fff;
	outline: none;
	border: none;
	cursor: pointer;
	
}
#user-address-info .user-address  .addressCon p label{
	margin-left: 10px;
	color: #F42760;
}
